@import '../../../../styles/variables.scss';

.day_info {
  position: relative;
  padding: 40px;
  max-width: 750px;
  width: 100%;
  box-shadow: $box-shadow;
  border-radius: $border-radius;
  background: var(#{--components-background-default});
}

.item {
  position: relative;
  display: flex;
  align-items: center;
  margin-bottom: 25px;

  &:last-of-type {
    margin-bottom: 0;
  }

  .indicator,
  .indicator_name {
    margin-right: 20px;
  }

  .indicator {
    width: 38px;
    height: 38px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    box-shadow: 1px 4px 10px -1px rgba(71, 147, 255, 0.2);
    border-radius: 50%;
  }

  .indicator_name,
  .indicator_value {
    font-size: 14px;
    z-index: 1;
  }

  .indicator_name {
    color: $base-color;
  }

  .indicator_value {
    color: var(#{--text-color-default});
  }
}

.cloud_img {
  position: absolute;
  top: 0;
  right: 0;
  border-radius: 7%;
}
@media (max-width: 575px) {
  .cloud_img {
    display: none;
  }
  .day_info {
    position: relative;
    margin-top: 10px;
    padding: 5px;
    max-width: 575px;
  }
  .item {
    margin-bottom: 10px;
    .indicator {
      width: 28px;
      height: 28px;
    }
    .indicator_name,
    .indicator_value {
      font-size: 12px;
      z-index: 1;
    }
    .indicator_name {
      display: none;
    }
  }
}
