@import '../../styles/variables.scss';

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 30px;
}

.wrapper {
  display: flex;
  align-items: center;
}

.change_theme,
.logo {
  margin-right: 20px;
  cursor: pointer;
}

.logo {
  svg {
    width: 65px;
    height: 65px;
  }
}

.title {
  text-transform: uppercase;
  font-weight: bold;
  font-size: 25px;
  color: $primary-color;
}

.link {
  text-decoration: none;
}

.input {
  margin-right: 8px;
  color: rgb(255, 255, 255);
  width: 200px;
  height: 26px;
  font-size: 18px;
  padding: 4px 0 4px 10px;
  border: 1px solid $base-color;
  background: $primary-color;
  border-radius: 8px;
}

.input::placeholder {
  color: rgba(255, 255, 255, 0.9);
}

.btn {
  width: 80px;
  height: 26px;
  font-size: 15px;
  padding: 4px 0 4px 0px;
  border: 1px solid $base-color;
  background: $base-color;
  border-radius: 8px;
  cursor: pointer;
}

@media (max-width: 575px) {
  .header {
    margin-bottom: 10px;
  }
  .title {
    display: none;
  }
  .logo {
    svg {
      width: 55px;
      height: 55px;
    }
    margin-left: 10px;
  }
  .input {
    width: 90%;
    height: 20px;
    font-size: 14px;
    margin-right: 20px;
    margin-bottom: 5px;
    float: right;
  }
  .btn {
    width: 80px;
    height: 20px;
    font-size: 14px;
    margin-right: 20px;
    padding: 0;
    float: right;
    color: white;
  }
}
